  <div id="login">
    <PasswordReset v-if="showPasswordReset" @close="togglePasswordReset()"></PasswordReset>
    <section>
      <div class="col1">
        <h1>Harsha Sample App</h1>
        <p>Welcome to the Harsha'a Sample VueJs web app powered by Vue.js and Firebase.</p>
      </div>
